import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './index.css';
class Devices extends Component {
  componentDidMount() {}
  render() {
    let pathName = window.location.hash.split('#')[1];

    return (
      <div className="devices">
        <h3>
          <Link to={'/home'} className="-color-dark">
            
          </Link>
          <span className="-color-light"> &nbsp;/&nbsp;</span>
          <span className="-color-light"></span>
          <span className="-color-light"> &nbsp;/&nbsp;</span>

          {pathName === '/devices/jia' && <span className="-color-light"></span>}
          {pathName === '/devices/zhuan' && <span className="-color-light"></span>}
          {pathName === '/devices/ya' && <span className="-color-light"></span>}
        </h3>
        {pathName === '/devices/jia' && (
          <div className="jia">
            <div className="cha-col">
              <div className="box">
                <span className="bg bg-jiagong" />
                <p>-JT-VL850</p>
              </div>
            </div>
            <div className="cha-col">
              <div className="box">
                <span className="bg bg-shukong" />
                <p>-X400</p>
              </div>
            </div>
          </div>
        )}
        {pathName == '/devices/zhuan' && (
          <div className="zhuan">
            <div className="mobile-zhuan-row">
              <div className="cha-col">
                <div className="box">
                  <span className="bg bg-zhuan3" />
                  <p>-MC500</p>
                </div>
              </div>
              <div className="cha-col">
                <div className="box">
                  <span className="bg bg-zhuan1" />
                  <p>-DT1000A</p>
                </div>
              </div>
            </div>
            <div className="cha-col">
              <div className="box">
                <span className="bg bg-zhuan2" />
                <p>-380Ti</p>
              </div>
            </div>
          </div>
        )}
        {pathName == '/devices/ya' && (
          <div className="ya">
            <div className="cha-col">
              <div className="box">
                <span className="bg bg-ya1" />
                <p>-DM180</p>
              </div>
            </div>
            <div className="cha-col">
              <div className="box">
                <span className="bg bg-ya2" />
                <p>-DM300</p>
              </div>
            </div>
            <div className="cha-col">
              <div className="box">
                <span className="bg bg-ya3" />
                <p>-DM500</p>
              </div>
            </div>
            <div className="cha-col">
              <div className="box">
                <span className="bg bg-ya4" />
                <p>-DM800</p>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}
export default Devices;



